import React, { Component } from 'react';
import Heading from 'grommet/components/Heading';
import Footer from 'grommet/components/Footer';

import Paragraph from 'grommet/components/Paragraph';

import Anchor from 'grommet/components/Anchor';
import Button from 'grommet/components/Button';
import Box from 'grommet/components/Box';
import GithubIcon from 'grommet/components/icons/base/SocialGithub';


export default class HomeHeader extends Component {
  render() {
    return (
      <div>
        <Box 
        primary={true} 
        pad='medium'
        // pad={{ vertical: 'large' }} 
        direction='column'
          justify='center' align='center' flex='grow'>
          <Heading align='center' tag="h1" style={{ color: '#07AEFF' }}  > </Heading>
          <Paragraph align='center' style={{ color: '#07AEFF' }}  >BINSTDBINARY DIGIT STANDARD</Paragraph>
          <Button label='' align='center' style={{ color: '#f5f5f5', border: '2px solid #f5f5f5' }} href='http://doc.binstd.com/' primary={true} />

        </Box>
        <Footer appCentered={true} justify='center'>
          <Anchor style={{ color: '#f5f5f5' }} href='https://github.com/binstd'  target="_blank" 
            icon={<GithubIcon />} />
         
          {/* <Anchor href='#'
            icon={this.weibotIcon()} /> */}
          {/* <Anchor href='#'
            icon={<WeibotIcon />} /> */}

          {/* <Anchor href='http://slackin.grommet.io'
              icon={<SlackIcon colorIndex='plain'
              a11yTitle='Grommet Slack' />}/> */}
        </Footer>
      </div>
    );
  }
};



